import React from 'react';
import { connect } from 'react-redux';
import Todos from '../Todos/Todos';
import s from "./Home.module.css"
export function Home(props) {
  return (
      <div className={s.container}>
        <Todos status="Todo"/>    
        <Todos status="InProgress"/>        
        <Todos status="Done" />
      </div>    
  )
};

export default Home;