import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actions from "../../actions/index"
import s from "./AddTodo.module.css"
// Nota 1: Para utilizar el hook `useState` para el manejo de estados de los inputs, tendras que utilizarlo de la siguiente manera
//React.useState

// Nota 2: En este componente tendras que usar la funcion `connect` de react-redux para conectarte al store. 
// Si usas el hook `useDispatch` no funcionaran los test.

export function AddTodo(props) {
  const [toDo, setToDo]= React.useState({
    title:"",
    description:"",
    place:"",
    date:""
  })
  function handleChange(e){
    setToDo({
      ...toDo,
      [e.target.name]:e.target.value
    })
  }
  function handleSubmit(e){
    e.preventDefault();
    props.addTodo(toDo)    
  }
  return (
    <section className ={s.container}>
      <form onSubmit={handleSubmit}>
        <label>Title:</label><br/>
          <input type="text" name="title" onChange={handleChange} placeholder="write a title..."></input><br/>
        <label>Description:</label><br/>
          <textarea  rows="10" cols="40" name="description" onChange={handleChange}  placeholder="write a description..."></textarea><br/>
        <label>Place:</label><br/>
          <input type="text" name="place" onChange={handleChange}  placeholder="write a place..."></input><br/>
        <label>Date:</label><br/>
          <input type="date" name="date" onChange={handleChange}></input><br/>
        <button type="submit" >Add</button>
      </form>
    </section>
  )
};
const mapDispatchToProps = (dispatch)=>{
  return bindActionCreators(actions, dispatch)
}

export default connect(null, mapDispatchToProps)(AddTodo)