import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actions from "../../actions/index"
import { useParams } from 'react-router';
import s from "./TodoDetails.module.css"
export function TodoDetails(props) {
  const {key}= useParams()
  const [todoX,setTodo]=React.useState({})
  useEffect(()=>{
    setTodo(props.toDoList.find((e)=>e.id==key))

  })

  function doneFunction(e){
    e.preventDefault();
    props.toDone(todoX.id)
  }
  function inProgressFunction(e){
    e.preventDefault();
    props.toInProgress(todoX.id)
  }

  return (
    <div className={s.container}>
      <section>
        <div>
          <h3>Title: {todoX.title}</h3><br/>
          <h5>Status: {todoX.status}</h5><br/>
          <p>Place: {todoX.place}<br/><span>Date: {todoX.date}</span></p>
        </div>
        <div>
          <p>Description: {todoX.description}</p><br/>        
        </div>
        </section>
      <section className={s.buttons}>
        <div >      
          <button onClick={(e)=>doneFunction(e)}>Done</button>
          <button onClick={(e)=>inProgressFunction(e)}>In progress</button>
        </div>
      </section>
    </div>
  )
};

const mapStateToProps = (state)=>{
  return{
    toDoList:state,
  }
}
const mapDispatchToProps = (dispatch)=>{
  return bindActionCreators (actions,dispatch)
}

export default connect(mapStateToProps,mapDispatchToProps)(TodoDetails);