// Podes usar esta variable para generar un ID por cada Todo.
let todoId = 0

 const addTodo_ = "ADD_TODO";

 const removeTodo_ = "REMOVE_TODO";

 const toInProgress_ = "TO_IN_PROGRESS";

 const toDone_ = "TO_DONE";

export function addTodo (data){ 
    todoId++
    return{
    type:"AddTodo",
    payload:{...data, id:todoId, status:"Todo"}
    }
}
export function removeTodo (data){
    return{
    type: "RemoveTodo",
    payload:data
    }
}
export function toInProgress (data){
    return{
    type:"ToInProgress",
    payload:data
    }
}
export function toDone (data){
    return{
    type:"ToDone",
    payload:data
    }
}