import React from 'react';
import {NavLink as Link } from 'react-router-dom';
import Todo from '../Todo/Todo';
import { connect } from 'react-redux';
import s from "./Todos.module.css"

export function Todos(props) {
  return (
    <div className={s.container}>
      <h3>{props.status}</h3>
      <div>
      {props.state.filter((e)=>e.status==props.status).map((data)=><Link className={s.link} to={`/edit/${data.id}`}><Todo title={data.title} key={data.id} key0={data.id}/></Link>)
      }</div>
      {/* <h3>In progress</h3>
      {props.toDo.filter((e)=>e.status=="In progress").map((data)=><Todo data={{...data}} key={data.key}/>)}
      <h3>Done</h3>
      {props.toDo.filter((e)=>e.status=="Done").map((data)=><Todo data={{...data}} key={data.key}/>)} */}
    </div>
  )
};
const mapStateToProps = state =>({
    state:state
  })
  

export default connect(mapStateToProps,null)(Todos);